import React, { InputHTMLAttributes, useCallback } from 'react';
import { UseFormRegisterReturn } from 'react-hook-form';
import {
  cep,
  cpf,
  currency,
  cnpj,
  maxLengthInput,
  percent
} from '../../utils/masks';
import { LabelCustom } from '../LabelCustom';
import { Container, IconWarning, Input } from './styles';

interface IProps extends InputHTMLAttributes<HTMLInputElement> {
  register: UseFormRegisterReturn;
  errors?: any;
  label?: string;
  mask?: 'cep' | 'currency' | 'cpf' | 'cnpj' | 'percent';
}

export const InputForm: React.FC<IProps> = ({
  register,
  errors,
  label,
  mask,
  ...rest
}) => {
  const handleKeyUp = useCallback(
    (e: React.FormEvent<HTMLInputElement>) => {
      if (mask === 'cep') {
        cep(e);
      }
      if (mask === 'currency') {
        currency(e);
      }
      if (mask === 'cpf') {
        cpf(e);
      }

      if (mask === 'cnpj') {
        cnpj(e);
      }

      if (mask === 'percent') {
        percent(e);
      }

      if (rest.maxLength) {
        maxLengthInput(e);
      }
    },
    [mask, rest.maxLength]
  );

  const a = { ...{ errors, label } };
  return (
    <Container
      className={`${rest.className} ${
        errors && errors[register.name] ? 'invalid' : ''
      }`}
    >
      <LabelCustom htmlFor={register?.name}>{label}</LabelCustom>

      <Input
        placeholder={rest.placeholder}
        type={rest.type}
        onClick={rest.onClick}
        disabled={rest.disabled}
        name={register?.name}
        id={register?.name}
        onBlur={register?.onBlur}
        onChange={register?.onChange}
        ref={register?.ref}
        onKeyUp={handleKeyUp}
        className={rest.type === 'checkbox' ? 'react-switch-checkbox' : ''}
        maxLength={rest.maxLength}
      />

      {rest.type && rest.type === 'checkbox' && (
        <label htmlFor={register?.name} className="react-switch-label">
          <span className="react-switch-button" />
        </label>
      )}

      <span>
        <IconWarning />
        {errors && errors[register.name]?.message}
      </span>
    </Container>
  );
};
