import { IQuoteItem } from '../Interfaces/QuotesDetail';
import { IShippingPayment } from '../Interfaces/ShippingPayment';
import {
  mocaShippingAndPayment,
  mocaQuotesItems
} from '../MoocasData/QuotesDetail';
import api from './api';

export interface IResponseLogin {
  data: IQuoteItem[];
  success: boolean;
  errors: any;
}

export const QuoteDetailService = {
  UpdateShippingAndPayment: (item: IShippingPayment) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    }),

  GetShippingAndPaymentForm: (quoteId: String) =>
    new Promise<IShippingPayment>(resolve =>
      setTimeout(() => {
        const result = mocaShippingAndPayment.find(q => q.QuoteId === quoteId);
        resolve(result || ({} as IShippingPayment));
      }, 2000)
    ),

  UpdateQuoteItem: (quoteItem: IQuoteItem) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    }),

  GetQuoteItems: async (
    quoteId: String,
    setLoading: (value: boolean) => void
  ) => {
    try {
      const {
        data: { data, success, errors }
      } = await api.get<IResponseLogin>(`QuoteItem/${quoteId}`);
      if (success) return data;
      throw errors;
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
    return null;
  }

  // new Promise<IQuoteItem[]>(resolve =>
  //   setTimeout(() => {
  //     const result = mocaQuotesItems.filter(q => q.QuoteId === quoteId);
  //     resolve(result?.length ? result : []);
  //   }, 2000)
  // )
};
