import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import {
  GoogleLogin,
  GoogleLoginResponse,
  GoogleLoginResponseOffline
} from 'react-google-login';

import { useMainContext } from 'Context/Main';

import { isAuthenticated } from 'Auth';
import { IResponseLogin, IUser } from 'Interfaces/User';
import { ProfileService } from 'services/profileService';
import api from 'services/api';
import { Container, Wrapper, LogoCompany, WapperTop } from './styles';

const Login: React.FC = () => {
  const { setProfile, profile, setLoading } = useMainContext();
  const refSelect = useRef<HTMLSelectElement>(null);
  const [unauthorized, setUnauthorized] = useState(false);
  const history = useHistory();

  const handleLoginSuccess = useCallback(
    async (response: GoogleLoginResponse | GoogleLoginResponseOffline) => {
      const {
        googleId,
        profileObj: { email, name, imageUrl }
      } = response as GoogleLoginResponse;

      setLoading(true);

      const resultProfile = await ProfileService.LoginService(
        googleId,
        email,
        setLoading
      );

      setLoading(false);

      if (resultProfile === null) setUnauthorized(true);
      else {
        setProfile({ ...resultProfile, Name: name, ImageUrl: imageUrl });
        history.push('/');
      }
    },
    [history, setLoading, setProfile]
  );

  const handleLoginError = useCallback(() => {}, []);
  const [company, setCompany] = useState('');

  useEffect(() => {
    // quando ja tiver um dominio, pegar o nome da empresa "lakatos" pelo subdominio
    // const companyName = window.location.hostname.split(".")[0];
    const companyName = 'lakatos';
    setCompany(`/images/${companyName}.svg`);
  }, [setProfile]);

  if (profile) return <Redirect to="/" />;

  return (
    <Container>
      <Wrapper>
        <WapperTop>
          <h1>Painel do Fornecedor</h1>
          <LogoCompany src={company} />
        </WapperTop>
        <GoogleLogin
          clientId={`${process.env.REACT_APP_GOOGLE_AUTH_CLIENT_ID}`}
          buttonText="Logar com Google"
          onSuccess={handleLoginSuccess}
          onFailure={handleLoginError}
          type="button"
        />

        {unauthorized && (
          <h2 className="unauthorized"> Login n√£o Autorizado</h2>
        )}
      </Wrapper>
    </Container>
  );
};

export default Login;
