import React from 'react';

import { Container, Title, Total } from './styles';
import {
  IPaymentConditionsSelect,
  IQuoteItem,
  IShippingTypeSelect
} from '../../Interfaces/QuotesDetail';

import { IShippingPayment } from '../../Interfaces/ShippingPayment';

import { ISupplier } from '../../Interfaces/Supplier';
import { ICompany } from '../../Interfaces/Company';
import { IQuote } from '../../Interfaces/Quotes';

const TablePrint: React.FC<{
  info: string[][];
  title: string;
}> = ({ info, title }) => (
  <>
    <h2>{title}</h2>
    <table>
      <tbody>
        {info.map((row, index) => (
          <tr key={index.toString()}>
            {row.map((col, indexCol) => (
              <td key={indexCol.toString()}> {col}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  </>
);

interface Props extends React.HTMLAttributes<HTMLDivElement> {
  quoteItems: IQuoteItem[] | null;
  supplier: ISupplier;
  company: ICompany;
  quote: IQuote | null;
  shipping: IShippingTypeSelect[];
  payment: IPaymentConditionsSelect[];
  shippingPayment: IShippingPayment;
}

const headerSupplierBuyer = [
  'Raz√£o Social',
  'Nome Fantasia',
  'CNPJ',
  'Endere√ßo',
  'Bairro',
  'Cidade',
  'Estado',
  'CEP'
];

const headerQuote = [
  'Tipo Frete',
  'Valor Frete',
  'Data Emiss√£o',
  'Data Limite',
  'Condi√ß√£o de Pagamento'
];

const headerQuoteItems = [
  'Item',
  'C√≥digo',
  'Descri√ß√£o',
  'C√≥digo Ref.',
  'Qtd.',
  'Valor Unit.',
  'Total',
  'Aliq. IPI%',
  'Aliq. ICMS%',
  'Observa√ß√£o'
];

const QuoteDetailPrint: React.ForwardRefRenderFunction<
  HTMLDivElement,
  Props
> = (
  { quoteItems, supplier, company, quote, shipping, payment, shippingPayment },
  ref
) => {
  const infoSupplier: string[][] = [[...headerSupplierBuyer]];
  const infoBuyer: string[][] = [[...headerSupplierBuyer]];
  const infoQuote: string[][] = [[...headerQuote]];
  const infoQuoteItems: string[][] = [[...headerQuoteItems]];

  infoSupplier.push([
    ...[
      supplier.companyName,
      supplier.fantasyName,
      supplier.cnpj,
      supplier.address,
      supplier.neighborhood,
      supplier.city,
      supplier.state,
      supplier.zipCode
    ]
  ]);

  infoBuyer.push([
    ...[
      company.CompanyName,
      company.FantasyName,
      company.Cnpj,
      company.Address,
      company.Neighborhood,
      company.City,
      company.State,
      company.ZipCode
    ]
  ]);
  const { ShippingTypeId } = shippingPayment;
  const shippingType =
    shipping.find(s => s.shippingTypeId === ShippingTypeId)?.shippingType || '';

  const paymentCondition =
    payment.find(
      p => p.paymentConditionsId === shippingPayment.PaymentConditionsId
    )?.paymentCondition || '';

  infoQuote.push([
    ...[
      shippingType,
      shippingPayment?.ShippingValue?.toString(),
      quote?.DtEmission.toDateBr() || '',
      quote?.DtLimit.toDateBr() || '',
      paymentCondition
    ]
  ]);

  quoteItems?.forEach(q =>
    infoQuoteItems.push([
      ...[
        q.ItemId,
        q.ProductCode,
        q.Product,
        q.ReferenceCode,
        q.Amount.toString(),
        q.UnitValue,
        q.Total,
        `${q.IpiValue}%`,
        `${q.IcmsValue}%`,
        q.Observation
      ]
    ])
  );

  const total = quoteItems?.reduce(
    (s, q, result) => (q?.Total?.toString().toFloat() || 0) + s,
    0
  );

  return (
    <Container ref={ref}>
      <Title>Cota√ß√£o - {quoteItems?.length && quoteItems[0].QuoteId}</Title>
      <TablePrint title="Fornecedor" info={infoSupplier} />
      <TablePrint title="Comprador" info={infoBuyer} />
      <TablePrint title="Cota√ß√£o" info={infoQuote} />
      <TablePrint title="Itens da Cota√ß√£o" info={infoQuoteItems} />
      <Total>
        <span>Total</span> <span>R$ {total}</span>
      </Total>
    </Container>
  );
};

export default React.forwardRef(QuoteDetailPrint);
