import React from 'react';

export function cep(e: React.FormEvent<HTMLInputElement>) {
  e.currentTarget.maxLength = 9;
  let { value } = e.currentTarget;
  value = value.replace(/\D/g, '');
  value = value.replace(/^(\d{5})(\d)/, '$1-$2');
  e.currentTarget.value = value;
  return e;
}

export function currency(e: React.FormEvent<HTMLInputElement>) {
  let { value } = e.currentTarget;
  value = value.replace(/\D/g, '');
  if (!value.length) {
    e.currentTarget.value = value;
    return e;
  }
  value = `${(value.toFloat() / 100).toFixed(2)}`;
  value = value.replace('.', ',');
  value = value.replace(/(\d)(\d{2})$/, '$1,$2');
  value = value.replace(/(?=(\d{3})+(\D))\B/g, '.');
  e.currentTarget.value = value;
  return e;
}

export function percent(e: React.FormEvent<HTMLInputElement>) {
  e.currentTarget.maxLength = 6;
  let { value } = e.currentTarget;
  value = value.replace(/\D/g, '');
  if (value.length > 6) return e;
  if (!value.length) {
    e.currentTarget.value = value;
    return e;
  }
  value = `${(value.toFloat() / 100).toFixed(2)}`;
  value = value.replace('.', ',');
  value = value.replace(/(\d)(\d{2})$/, '$1,$2');
  if (value.toFloat() > 100) value = '100,00';
  e.currentTarget.value = value;
  return e;
}

export function cpf(e: React.FormEvent<HTMLInputElement>) {
  e.currentTarget.maxLength = 14;
  let { value } = e.currentTarget;
  if (!value.match(/^(\d{3}).(\d{3}).(\d{3})-(\d{2})$/)) {
    value = value.replace(/\D/g, '');
    value = value.replace(/(\d{3})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d{2})$/, '$1-$2');
    e.currentTarget.value = value;
  }
  return e;
}

export function cnpj(e: React.FormEvent<HTMLInputElement>) {
  e.currentTarget.maxLength = 18;
  let { value } = e.currentTarget;
  if (!value.match(/^(\d{2}).(\d{3}).(\d{3})\/(\d{4})-(\d{2})$/)) {
    value = value.replace(/\D/g, '');
    value = value.replace(/(\d{2})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d)/, '$1/$2');
    value = value.replace(/(\d{4})(\d{2})$/, '$1-$2');
    e.currentTarget.value = value;
  }
  return e;
}

export function maxLengthInput(e: React.FormEvent<HTMLInputElement>) {
  const { maxLength, value } = e.currentTarget;
  if (value.length > maxLength)
    e.currentTarget.value = value.slice(0, maxLength);
  return e;
}
