/* eslint-disable react-hooks/exhaustive-deps */
import React, { createContext, useCallback, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { ProfileService } from 'services/profileService';
import { IUser } from '../../Interfaces/User';

interface IMainContext {
  menuOpen: boolean;
  setMenuOpen: (value: boolean) => void;
  loading: boolean;
  setLoading: (value: boolean) => void;
  profile: IUser | null;
  setProfile: (value: IUser | null) => void;
}

const MainContext = createContext<IMainContext>({} as IMainContext);

export const MainContextProvider: React.FC = ({ children }) => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [profile, setProfile] = useState<IUser | null>(null);

  const handleSetLoading = useCallback((value: boolean) => {
    setLoading(value);
  }, []);

  const handleSetMenuOpen = useCallback((value: boolean) => {
    // localStorage.setItem('menu-open', JSON.stringify(value));
    setMenuOpen(value);
  }, []);

  const handleSetProfile = useCallback((value: IUser | null) => {
    setProfile(value);
    // if (!value) localStorage.removeItem('profile');
    // else localStorage.setItem('profile', JSON.stringify(value));
  }, []);

  useEffect(() => {
    // const profileInStorage = localStorage.getItem('profile');
    // if (profileInStorage) {
    //   setProfile(JSON.parse(profileInStorage));
    // } else {
    //   setProfile(null);
    // }
    const refreshToken = async () => {
      setLoading(true);
      const resultProfile = await ProfileService.RefreshToken(setLoading);
      setProfile(resultProfile);
    };
    refreshToken();
  }, []);

  return (
    <MainContext.Provider
      value={{
        menuOpen,
        setMenuOpen: handleSetMenuOpen,
        loading,
        setLoading: handleSetLoading,
        profile,
        setProfile: handleSetProfile
      }}
    >
      {children}
    </MainContext.Provider>
  );
};

export const useMainContext = () => React.useContext(MainContext);
