import React, { useCallback, useEffect, useState } from 'react';
import { handleScrool } from '../../utils/scrool';
import {
  CenterPanel,
  Column,
  LabelCustom,
  LeftPanel,
  RightPanel,
  Row,
  RowPanel,
  Text
} from '..';
import { useContextQuoteItems } from '../../Context/QuoteItems';
import { Panel } from '../Panel';
import QuotesItemsForm from '../QuotesItemsForm';
import { ArrowDownIcon, ArrowUpIcon, Container, TagIcon } from './styles';
import { IQuoteItem } from '../../Interfaces/QuotesDetail';
import { Button } from '../Button';

const QuotesItems: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  ...rest
}) => {
  const [quoteItemId, setQuoteItemId] = useState<string>('');
  const [quoteItems, setQuoteItems] = useState<IQuoteItem[]>();

  const { quoteItemsContext } = useContextQuoteItems();

  const handleCollapse = useCallback((id: string) => {
    setQuoteItemId(prevState => (prevState === id ? '' : id));

    handleScrool(id);
  }, []);

  useEffect(() => {
    setQuoteItems(
      quoteItemsContext?.slice(0, Math.min(10, quoteItemsContext.length))
    );
  }, [quoteItemsContext]);

  const handleLoadMore = useCallback(() => {
    if (quoteItemsContext)
      setQuoteItems(prevState => ({
        ...prevState,
        ...quoteItemsContext?.slice(
          prevState?.length,
          Math.min(10, quoteItemsContext.length)
        )
      }));
  }, [quoteItemsContext]);

  return (
    <Container {...{ rest }}>
      {!quoteItems && (
        <>
          <br />
          <Panel backgroundColor="panel-warning">
            N√£o foi poss√≠vel encontrar os itens para essa Cota√ß√£o
          </Panel>
        </>
      )}
      {console.log(quoteItems)}
      {quoteItems?.map(
        ({ Id: id, ItemId: itemId, Product: product, Total: total }) => (
          <RowPanel
            key={id}
            id={id}
            onClick={() => handleCollapse(id)}
            className={
              quoteItemId === '' || quoteItemId === id
                ? 'rowPanel'
                : ' hide rowPanel'
            }
          >
            <LeftPanel>
              <TagIcon />
            </LeftPanel>
            <CenterPanel>
              <Row>
                <Column>
                  <LabelCustom>Item</LabelCustom>
                  <Text>{itemId}</Text>
                </Column>
              </Row>
              <Row>
                <Column>
                  <LabelCustom>Produto</LabelCustom>
                  <Text colorFont="blue-light">{product}</Text>
                </Column>

                <Column width="50%">
                  {total ? (
                    <div>
                      <LabelCustom>Total</LabelCustom>
                      <Text>R$ {total}</Text>
                    </div>
                  ) : (
                    ''
                  )}
                </Column>
              </Row>
            </CenterPanel>
            <RightPanel>
              <ArrowUpIcon className={quoteItemId === id ? 'active' : ''} />
              <ArrowDownIcon className={quoteItemId === id ? '' : 'active'} />
            </RightPanel>
          </RowPanel>
        )
      )}
      {quoteItems && quoteItems.length > 10 && (
        <Row>
          <Column align="center">
            <Button width="auto" outlined>
              Carregar Mais ...
            </Button>
          </Column>
        </Row>
      )}
      {quoteItemId && (
        <QuotesItemsForm
          quoteItemId={quoteItemId}
          handlerCollapse={handleCollapse}
        />
      )}
    </Container>
  );
};

export default React.memo(QuotesItems);
