import { IQuote } from '../Interfaces/Quotes';
import { mocaQuotes } from '../MoocasData/Quotes';
import api from './api';

interface Param {
  id?: string;
  userId?: string;
  days?: number;
  statusQuoteId?: string;
  setLoading: (value: boolean) => void;
}

interface Data {
  quotes: IQuote[];
}

export interface IResponseQuotes {
  data: IQuote[];
  success: boolean;
  errors: any;
}

export interface IResponseQuote {
  data: IQuote;
  success: boolean;
  errors: any;
}

export const QuoteService = {
  GetAll: async ({
    statusQuoteId = '0',
    days = 0,
    id,
    userId,
    setLoading
  }: Param) => {
    try {
      const {
        data: { data, success, errors }
      } = await api.get<IResponseQuotes>(`Quote/GetAllByUser/${userId}`);

      if (success) return data;
      throw errors;
    } catch (error) {
      setLoading(false);
      console.log('error', error);
    }
    return null;
  },
  GetById: async (id: string, setLoading: (value: boolean) => void) => {
    try {
      const {
        data: { data, success, errors }
      } = await api.get<IResponseQuote>(`Quote/Get/${id}`);

      if (success) return data;
      throw errors;
    } catch (error) {
      setLoading(false);
      console.log('error', error);
    }
    return null;
  }
};
