import React, { createContext, useCallback, useContext, useState } from 'react';
import { IQuoteItem } from '../../Interfaces/QuotesDetail';

interface IQuoteItemsContext {
  quoteItemsContext: IQuoteItem[] | null;
  setQuoteItemsContext: (items: IQuoteItem[] | null) => void;
}

const ContextQuoteItems = createContext<IQuoteItemsContext>(
  {} as IQuoteItemsContext
);

export const ContextQuoteItemsProvider: React.FC = ({ children }) => {
  const [quoteItems, setQuoteItems] = useState<IQuoteItem[] | null>([]);

  const handleSetQuoteItems = useCallback((items: IQuoteItem[] | null) => {
    setQuoteItems(items);
  }, []);

  return (
    <ContextQuoteItems.Provider
      value={{
        quoteItemsContext: quoteItems,
        setQuoteItemsContext: handleSetQuoteItems
      }}
    >
      {children}
    </ContextQuoteItems.Provider>
  );
};

export const useContextQuoteItems = () => useContext(ContextQuoteItems);
